import Glide from '@glidejs/glide'
const carousel = [...document.querySelectorAll('.main-carousel')]
const category = [...document.querySelectorAll('.category-list')]
const banners = [...document.querySelectorAll('.banner')]

document.addEventListener('DOMContentLoaded', () => {
  carousel.forEach(element => {
    new Glide(element, {
      type: 'carousel',
      startAt: 0,
      perView: 1,
      gap: 0,
      autoplay: 5000,
      hoverpause: true,
      animationDuration: 500
    }).mount()
  })

  category.forEach(element => {
    new Glide(element, {
      type: 'carousel',
      startAt: 0,
      perView: 3,
      gap: 40,
      autoplay: 5000,
      hoverpause: true,
      animationDuration: 500,
      breakpoints: {
        768: {
          gap: 16
        }
      }
    }).mount()
  })

  banners.forEach(element => {
    new Glide(element, {
      type: 'carousel',
      startAt: 0,
      perView: 1,
      gap: 0,
      autoplay: 5000,
      hoverpause: true,
      animationDuration: 500
    }).mount()
  })
})
