document.addEventListener('DOMContentLoaded', () => {
  const searchField = [...document.querySelectorAll('.search-field')]

  searchField.forEach(element => {
    const field = element.querySelector('input')
    const list = element.querySelector('ul')
    const items = [...list.children]
    const drop = element.querySelector('i')
    const wrapper = element.parentElement
    const inputFiltering = (e) => {
      e.preventDefault()
      const target = e.target
      const regexp = new RegExp(target.value || target.textContent, 'i')
      const filterList = [...items.filter(item => regexp.test(item.textContent))]
      element.classList.add('city-modal__field_drop')
      list.classList.add('city-list__opened')
      list.innerHTML = ''
      filterList.forEach(el => list.appendChild(el))
    }

    field.addEventListener('input', inputFiltering)

    drop.addEventListener('click', (e) => {
      e.preventDefault()

      element.classList.toggle('city-modal__field_drop')
      list.classList.toggle('city-list__opened')
    })

    wrapper.addEventListener('click', (e) => {
      e.preventDefault()
      const target = e.target
      if (target.classList.contains('city-modal')) {
        element.classList.remove('city-modal__field_drop')
        list.classList.remove('city-list__opened')
      }
    })

    items.forEach(item => {
      item.addEventListener('click', (e) => {
        const target = e.target

        inputFiltering(e)
        field.value = target.textContent
        element.classList.remove('city-modal__field_drop')
        list.classList.remove('city-list__opened')
      })
    })
  })
})
